import { Component } from '@angular/core';
import { AppState } from '../../../../store/state/app.state';
import { Store } from '@ngrx/store';
import { MatDialogRef } from '@angular/material/dialog';
import { map } from 'rxjs/operators';
import { selectProductsTypeStorageUpgrade } from '../store/selectors/products.selectors';
import { Observable } from 'rxjs';
import { Product } from 'domain-entities';

@Component({
	selector: 'app-storage-options-dialog',
	templateUrl: './storage-options-dialog.component.html',
	styleUrls: ['./storage-options-dialog.component.scss'],
})
export class StorageOptionsDialogComponent {

	storageUpgradeProducts$: Observable<Product[]>;

	constructor(
		private readonly store: Store<AppState>,
		private readonly dialogRef: MatDialogRef<StorageOptionsDialogComponent>,
	) {
		this.initProducts();
	}

	close(): void {
		this.dialogRef.close();
	}

	private initProducts(): void {
		this.storageUpgradeProducts$ =
			this.store.select(selectProductsTypeStorageUpgrade).pipe(
				map(products => {
					const prods = [...products];
					prods.sort((a, b) => a.grade - b.grade);
					return prods;
				}));
	}
}
