import { createFeatureSelector, createSelector } from '@ngrx/store';
import { ProductsState } from '../state/products.state';
import { productsFeatureKey } from '../reducers/products.reducer';
import { ProductType } from 'domain-entities';

export const selectProductsState = createFeatureSelector<ProductsState>(productsFeatureKey);

export const selectProductsTypeSubscription = createSelector(
	selectProductsState,
	(products) => products.filter(product => product.type === ProductType.SUBSCRIPTION),
);

export const selectProductsTypeStorageUpgrade = createSelector(
	selectProductsState,
	(products) => products.filter(product => product.type === ProductType.STORAGE_UPGRADE));

export const selectProductsTypeSubscriptionAndTrial = createSelector(
	selectProductsState,
	(products) => products.filter(product => product.type === ProductType.SUBSCRIPTION || product.type === ProductType.TRIAL));
