import { Component, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { CompanyLimit, Plan, PlanPaymentInterval, Product, ProductType, Subscription } from 'domain-entities';
import { BehaviorSubject, combineLatest, Observable, Subject } from 'rxjs';
import { filter, map, mergeMap, shareReplay, switchMap, takeUntil, tap } from 'rxjs/operators';
import { AuthService } from '../../../../shared/firebase/auth.service';
import { CompanyService } from '../../../../shared/firebase/company.service';
import { BillingDetails, CouponResponse, SubscriptionService } from '../../../../shared/firebase/subscription.service';
import { ProfileService } from '../../../../shared/firebase/profile.service';
import { Company } from '../../../../shared/models/company.model';
import { ProductService } from '../../../../shared/firebase/product.service';
import { DecimalPipe } from '@angular/common';
import { StorageData } from './model/StorageData';
import { OverviewData } from './model/OverviewData';
import { PaymentIntervalChange } from '../products/product-confirm/model/ProductChange';
import { ProductChangeService } from '../products/product-change.service';
import { SubscriptionChangeType } from '../products/model/SubscriptionChangeType';
import { EmployeeChange } from '../products/manage-company-employess/model/EmployeeChange';
import { SubscriptionChangeTranslationService } from './subscription-change-translation.service';
import { SubscriptionChangeStatus } from '../products/model/SubscriptionChangeStatus';
import { getCost, getOverallCostPerUser, getVatRateForCountry } from '../products/shared/shared.functions';
import { HotjarTrackingService } from '../../../third-party/hotjar-tracking/services/hotjar-tracking.service';
import { TrackEventType } from '../../../../shared/models/track-event.type';
import { AppState } from '../../../../store/state/app.state';
import { Store } from '@ngrx/store';
import { selectCompanyLimits } from '../../../../store/selectors/app.selectors';
import { MatDialog } from '@angular/material';
import { StorageOptionsDialogComponent } from '../storage-options-dialog/storage-options-dialog.component';
import { selectProductsTypeSubscriptionAndTrial } from '../store/selectors/products.selectors';
import moment from 'moment';

const ONE_GIGABYTE = 1e9;

@Component({
	selector: 'app-subscription',
	templateUrl: './subscription.component.html',
	styleUrls: ['../../../../account/settings/settings.component.scss', './subscription.component.css'],
	providers: [DecimalPipe],
})
export class SubscriptionComponent implements OnInit, OnDestroy {
	readonly PlanPaymentInterval = PlanPaymentInterval;

	company$: Observable<Company>;
	companyLimits$: Observable<CompanyLimit> = this.store.select(selectCompanyLimits);
	subscription$: Subject<Subscription> = new BehaviorSubject<Subscription>(null);
	billingDetails$: Subject<BillingDetails> = new BehaviorSubject<BillingDetails>(null);
	products$: Observable<Product[]> = this.store.select(selectProductsTypeSubscriptionAndTrial);
	overviewData$: Observable<OverviewData>;
	isTrial$: Observable<boolean>;


	paymentIntervalChange$: Observable<PaymentIntervalChange>;
	employeesChange$: Observable<EmployeeChange>;

	editUser = false;
	editPaymentInterval = false;

	TrackEventType = TrackEventType;

	private onDestroy$ = new Subject();
	public loading = true;

	constructor(
		private subscriptionService: SubscriptionService,
		private companyService: CompanyService,
		private authService: AuthService,
		private profileService: ProfileService,
		private productService: ProductService,
		private productChangeService: ProductChangeService,
		private subscriptionChangeTranslationService: SubscriptionChangeTranslationService,
		private translate: TranslateService,
		private decimalPipe: DecimalPipe,
		private readonly hotjarTrackingService: HotjarTrackingService,
		private readonly dialog: MatDialog,
		private readonly store: Store<AppState>,
	) {
	}

	ngOnInit(): void {
		this.setupCompany();
		this.setupSubscription();
		this.setupOverviewData();
		this.setupBillingDetails();
		this.setupPaymentIntervalChange();
		this.setupEmployeesChange();
		this.setupTrial();
	}

	ngOnDestroy(): void {
		this.onDestroy$.next();
		this.onDestroy$.complete();

	}

	setupCompany(): void {
		const profile$ = this.authService.$authState.pipe(
			filter(user => !!user),
			mergeMap(user => this.profileService.getProfile(user.uid)), shareReplay());
		this.company$ = profile$.pipe(
			mergeMap(profile => this.companyService.getCompany(profile.company)),
			shareReplay());
	}

	setupSubscription(): void {
		this.subscriptionService.getActiveSubscription().pipe(takeUntil(this.onDestroy$)).subscribe(subscription => {
			this.subscription$.next(subscription);
		});
	}

	setupBillingDetails(): void {
		this.subscriptionService.getActiveSubscriptionAndProduct()
			.pipe(
				filter(([subscription, product]) => subscription && product.type !== ProductType.TRIAL),
				switchMap(([subscription]) => this.subscriptionService.getBillingInformation(subscription.id)),
				filter(billingDetails => !!billingDetails),
				takeUntil(this.onDestroy$),
			).subscribe(billingDetails => this.billingDetails$.next(billingDetails));
	}

	setupOverviewData(): void {
		this.overviewData$ = combineLatest([
			this.company$,
			this.subscription$,
			this.companyLimits$,
			this.products$,
			this.billingDetails$],
		).pipe(switchMap(async params => {
			const [company, subscription, companyLimits, products, billingDetails] = params;
			const filteredProducts = products.filter(prod => prod.type === ProductType.SUBSCRIPTION || prod.type === ProductType.TRIAL);
			// The product with the lowest grade is implicitly defined as the Basic (i.e. free tier) product)
			const product: Product = products.sort((a, b) => a.grade - b.grade)[0];
			const result: OverviewData = {
				company,
				product,
				allProducts: products,
			};
			if (companyLimits) {
				const storageData = createStorageData(company, companyLimits);
				result.companyLimits = companyLimits;
				result.storageData = storageData;
			}
			if (subscription) {
				const allPlans: Plan[] = [];
				products.filter(prod => prod.plans).forEach(prod => allPlans.push(...prod.plans));

				const planOfSubscription = allPlans.find(plan => plan.stripeId === subscription.currentState.planId);
				const productOfSubscription = filteredProducts.find(prod => prod.plans.find(plan => plan === planOfSubscription));
				const numberOfUsers = subscription.currentState.quantity;
				let price: string;
				let pricePerUser: string;
				if (billingDetails) {
					const couponResponse = await this.subscriptionService.getOrLoadCouponResponse(planOfSubscription.stripeId);
					price = this.getPrice(numberOfUsers, planOfSubscription, billingDetails, couponResponse, subscription.renewalDate);
					pricePerUser = this.getPricePerUser(numberOfUsers, planOfSubscription, billingDetails, couponResponse, subscription.renewalDate);
				}

				result.plan = planOfSubscription;
				result.product = productOfSubscription;
				result.allProducts = products;
				result.price = price;
				result.pricePerUser = pricePerUser;
				result.subscription = subscription;
				result.numberOfUsers = numberOfUsers;
				result.billingDetails = billingDetails;
			}
			return result;
		}));
	}

	setupPaymentIntervalChange(): void {
		this.paymentIntervalChange$ = this.productChangeService.getProductChanges().pipe(
			filter(change => change.changeType === SubscriptionChangeType.PAYMENT_INTERVAL),
			map(change => change as PaymentIntervalChange), tap(change => {
				if (change.status === SubscriptionChangeStatus.CONFIRMED) {
					this.editPaymentInterval = false;
				}
			}));
	}

	setupEmployeesChange(): void {
		this.employeesChange$ = this.productChangeService.getProductChanges().pipe(
			filter(change => change.changeType === SubscriptionChangeType.EMPLOYEES_CHANGE),
			map(change => change as EmployeeChange), tap(change => {
				if (change.status === SubscriptionChangeStatus.CONFIRMED) {
					this.editUser = false;
				}
			}));
	}

	public getRenewalText(overviewData: OverviewData): string {
		return this.subscriptionChangeTranslationService.getProductChangeText(
			overviewData.product, overviewData.allProducts, overviewData.subscription);
	}

	public getPrice(numberOfUsers: number, plan: Plan, billingDetails: BillingDetails, couponResponse: CouponResponse, renewalDate: number): string {
		const timeUnit = plan.paymentInterval === PlanPaymentInterval.MONTHLY ? 'months' : 'years';
		const dateOfPeriodStart = moment.unix(renewalDate).subtract(1, timeUnit).unix();
		const vatRate = getVatRateForCountry(billingDetails.billing.address.country, plan, dateOfPeriodStart);
		const cost = getCost(plan, numberOfUsers, vatRate, { response: couponResponse, ignoreValid: true });
		const type = plan.paymentInterval.toLowerCase();
		const costText = this.subscriptionChangeTranslationService.getCurrencyCostText(cost);
		return this.translate.instant(`subscription.cost.${type}`, { cost: costText });
	}

	public getPricePerUser(numberOfUsers: number, plan: Plan, billingDetails: BillingDetails, couponResponse: CouponResponse, renewalDate: number): string {
		const type = plan.paymentInterval.toLowerCase();
		const timeUnit = plan.paymentInterval === PlanPaymentInterval.MONTHLY ? 'months' : 'years';
		const dateOfPeriodStart = moment.unix(renewalDate).subtract(1, timeUnit).unix();
		const vatRate = getVatRateForCountry(billingDetails.billing.address.country, plan, dateOfPeriodStart);
		const cost = getCost(plan, numberOfUsers, vatRate, { response: couponResponse, ignoreValid: true });
		const overallCostPerUser = getOverallCostPerUser(cost, numberOfUsers);
		const costText = this.subscriptionChangeTranslationService.getCurrencyCostText(overallCostPerUser);
		return this.translate.instant(`subscription.cost.perUser.${type}`, { cost: costText });
	}

	public getGbString(usedGBytes: number): string {
		return this.decimalPipe.transform(usedGBytes, '1.2-2');
	}

	public getPaymentIntervalChangeText(subscription: Subscription, products: Product[]): string {
		return this.subscriptionChangeTranslationService.getPaymentIntervalChangeText(subscription, products);
	}

	public getLicensesChangeText(subscription: Subscription): string {
		return this.subscriptionChangeTranslationService.getLicensesChangeText(subscription);
	}


	toggleEditUser(): void {
		this.editUser = !this.editUser;
	}

	toggleEditPaymentInterval(): void {
		this.editPaymentInterval = !this.editPaymentInterval;
	}

	undoCancellation(subscription: Subscription): void {
		this.subscriptionService.undoPlanChange(subscription);
	}

	setupTrial(): void {
		this.isTrial$ = this.overviewData$.pipe(map(data => data.product.type === ProductType.TRIAL));
	}

	openStorageOptions(): void {
		this.dialog.open(StorageOptionsDialogComponent, { width: '1190px', panelClass: 'storage-upgrade-dialog' });
	}

}

const createStorageData: (company: Company, companyLimit: CompanyLimit) => StorageData =
	(company: Company, companyLimit: CompanyLimit): StorageData => {
		const availableGbs = companyLimit.storageGBytes || 0;
		const usedGbs = (company.storageBytes || 0) / ONE_GIGABYTE;
		const gbPercentage = (usedGbs / availableGbs) * 100;
		return { availableGbs, usedGbs, gbPercentage };
	};
