import { Component, Input } from '@angular/core';
import { PlanPaymentInterval } from 'domain-entities';
import { ProductChangeService } from '../../products/product-change.service';
import { PaymentIntervalChange } from '../../products/product-confirm/model/ProductChange';
import { SubscriptionChangeStatus } from '../../products/model/SubscriptionChangeStatus';
import { TranslateService } from '@ngx-translate/core';
import { SubscriptionChangeWithExistingSubscription } from '../../products/model/SubscriptionChange';
import { SubscriptionChangeTranslationService } from '../subscription-change-translation.service';
import { getCostForSubscriptionChange, getVatRateForCountry } from '../../products/shared/shared.functions';
import { TrackingDispatcherService } from '../../../../../shared/tracking/tracking-dispatcher.service';
import { TrackEventType } from '../../../../../shared/models/track-event.type';
import { SubscriptionChangeDirection } from '../../products/model/SubscriptionChangeDirection';
import moment from 'moment';

@Component({
	selector: 'app-manage-payment-interval',
	templateUrl: './manage-payment-interval.component.html',
	styleUrls: ['./manage-payment-interval.component.scss'],
})
export class ManagePaymentIntervalComponent {
	private _selectedPaymentInterval: PlanPaymentInterval = PlanPaymentInterval.MONTHLY;

	@Input() set currentlySubscribedInterval(currentlySubscribedInterval: PlanPaymentInterval) {
		this.selectedPaymentInterval = currentlySubscribedInterval;
	}

	get selectedPaymentInterval(): PlanPaymentInterval {
		return this._selectedPaymentInterval;
	}

	locked = false;
	costText = '';
	changeDateText = '';

	set selectedPaymentInterval(selectedPaymentInterval: PlanPaymentInterval) {
		if (this.locked) {
			return;
		}
		this._selectedPaymentInterval = selectedPaymentInterval;
		this.productChangeService.updatePaymentInterval(this.selectedPaymentInterval);
	}

	private _paymentIntervalChange: PaymentIntervalChange;
	@Input() set paymentIntervalChange(paymentIntervalChange: PaymentIntervalChange) {
		if (!paymentIntervalChange) {
			return;
		}
		if (paymentIntervalChange.status === SubscriptionChangeStatus.PROCESSING) {
			this.locked = true;
			return;
		}
		if (paymentIntervalChange.status === SubscriptionChangeStatus.CONFIRMED) {
			this.locked = false;
		}
		this.costText = this.getChangeText(paymentIntervalChange);
		this.changeDateText = this.getChangeDateText(paymentIntervalChange);
		this._paymentIntervalChange = paymentIntervalChange;
	}

	constructor(private readonly translateService: TranslateService,
							private readonly productChangeService: ProductChangeService,
							private readonly subscriptionChangeTranslationService: SubscriptionChangeTranslationService,
							private readonly trackingDispatcherService: TrackingDispatcherService) {
	}

	readonly PlanPaymentInterval = PlanPaymentInterval;

	getChangeText(paymentIntervalChange: SubscriptionChangeWithExistingSubscription): string {
		const referenceDate = paymentIntervalChange.changeDirection === SubscriptionChangeDirection.DOWNGRADE ? paymentIntervalChange.subscription.renewalDate : moment().unix();
		const vatRate = getVatRateForCountry(paymentIntervalChange.billingDetails.billing.address.country, paymentIntervalChange.to.plan, referenceDate);
		const cost = getCostForSubscriptionChange(paymentIntervalChange, vatRate);
		return this.subscriptionChangeTranslationService.getCostChangeText(paymentIntervalChange, cost, vatRate);
	}

	getChangeDateText(paymentIntervalChange: SubscriptionChangeWithExistingSubscription): string {
		return this.subscriptionChangeTranslationService.getDateChangeText(paymentIntervalChange);
	}


	onConfirm(): void {
		this.trackingDispatcherService.emitTrackingEvent(TrackEventType.SUBSCRIPTION_OVERVIEW_INTERVAL_CONFIRM);
		if (this.locked || !this._paymentIntervalChange) {
			return;
		}
		this.productChangeService.confirmSubscriptionChange(this._paymentIntervalChange);
	}

}
