import { Component, Inject, Input, LOCALE_ID } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { DateService } from '../../../../../shared/helper/date.service';
import { ProductChange } from './model/ProductChange';
import { ProductChangeService } from '../product-change.service';
import { SubscriptionChangeType } from '../model/SubscriptionChangeType';
import { SubscriptionChangeStatus } from '../model/SubscriptionChangeStatus';
import { SubscriptionChangeTranslationService } from '../../subscription/subscription-change-translation.service';
import { getCostForSubscriptionChange, getVatRateForCountry } from '../shared/shared.functions';
import { TrackingDispatcherService } from '../../../../../shared/tracking/tracking-dispatcher.service';
import { SubscriptionChangeDirection } from '../model/SubscriptionChangeDirection';
import { TrackEventType } from '../../../../../shared/models/track-event.type';
import moment from 'moment';

interface ChangeText {
	questionPartOne: string;
	questionPartTwo: string;
	questionPartThree: string;

	dateText?: string;
	costText: string;
}

@Component({
	selector: 'app-product-confirm',
	templateUrl: './product-confirm.component.html',
	styleUrls: ['./product-confirm.component.scss', '../products.component.scss', '../../../../../account/settings/settings.component.scss'],
})
export class ProductConfirmComponent {

	changeText: ChangeText;
	private _productChange: ProductChange;

	@Input() set productChange(productChange: ProductChange) {
		if (!productChange || productChange.changeType !== SubscriptionChangeType.PRODUCT_CHANGE) {
			return;
		}
		if (productChange.status === SubscriptionChangeStatus.CONFIRMED) {
			this.changeText = null;
			this.locked = false;
			return;
		} else if (productChange.status === SubscriptionChangeStatus.PROCESSING) {
			// ignore new change requests if an existing request is processing
			this.locked = true;
			this.trackProductChange(productChange);
			return;
		}
		this._productChange = productChange;
		this.trackProductChange(productChange);
		this.setChangeText();
	}

	get productChange(): ProductChange {
		return this._productChange;
	}

	locked = false;

	constructor(
		private readonly dateService: DateService,
		private readonly translateService: TranslateService,
		private readonly productChangeService: ProductChangeService,
		private readonly subscriptionChangeTranslationService: SubscriptionChangeTranslationService,
		private readonly trackingDispatcherService: TrackingDispatcherService,
		@Inject(LOCALE_ID) private readonly  localeId) {
	}

	private setChangeText(): void {
		const questionPartOne = this.translateService.instant('subscription.change.questionPartOne');
		const questionPartTwo = this.translateService.instant('subscription.change.questionPartTwo');
		const questionPartThree = this.translateService.instant('subscription.change.questionPartThree');
		const referenceDate = this.productChange.changeDirection === SubscriptionChangeDirection.DOWNGRADE ? this.productChange.subscription.renewalDate : moment().unix();
		const vatRate = getVatRateForCountry(this.productChange.billingDetails.billing.address.country, this.productChange.to.plan, referenceDate);
		const cost = getCostForSubscriptionChange(this.productChange, vatRate);
		const costText = this.subscriptionChangeTranslationService.getCostChangeText(this.productChange, cost, vatRate);
		const dateText = this.subscriptionChangeTranslationService.getDateChangeText(this.productChange);

		const text = {
			questionPartOne,
			questionPartTwo,
			questionPartThree,
			costText,
			dateText,
		};
		this.changeText = text;
	}

	onConfirm(): void {
		this.productChangeService.confirmSubscriptionChange(this.productChange);
	}

	private trackProductChange(productChange: ProductChange): void {
		const newProduct = productChange.to.product;
		const changeDirection = productChange.changeDirection;
		const confirmed = productChange.status === SubscriptionChangeStatus.PROCESSING;
		let event: TrackEventType;
		if (confirmed && changeDirection === SubscriptionChangeDirection.UPGRADE) {
			event = TrackEventType.SUBSCRIPTION_PRODUCTS_UPGRADE_CONFIRM;
		} else if (!confirmed && changeDirection === SubscriptionChangeDirection.UPGRADE) {
			event = TrackEventType.SUBSCRIPTION_PRODUCTS_UPGRADE_OPEN;
		} else if (confirmed && changeDirection === SubscriptionChangeDirection.DOWNGRADE) {
			event = TrackEventType.SUBSCRIPTION_PRODUCTS_DOWNGRADE_CONFIRM;
		} else if (!confirmed && changeDirection === SubscriptionChangeDirection.DOWNGRADE) {
			event = TrackEventType.SUBSCRIPTION_PRODUCTS_DOWNGRADE_OPEN;
		}
		if (!event) {
			return;
		}

		let product: string;
		if (newProduct.grade === 0) {
			product = 'basic';
		} else if (newProduct.grade === 1) {
			product = 'pro';
		} else if (newProduct.grade === 2) {
			product = 'premium';
		}
		if (!product) {
			return;
		}

		this.trackingDispatcherService.emitTrackingEvent(event, { type: product });
	}
}
