import { Component, Input, OnChanges } from '@angular/core';
import { getChangeDirectionWithUnchanged, getLowestGradeProduct } from '../shared/shared.functions';
import { SubscriptionChangeDirection } from '../model/SubscriptionChangeDirection';
import { ProductChangeService } from '../product-change.service';
import { Product } from 'domain-entities';


@Component({
	selector: 'app-product-change',
	templateUrl: './product-change.component.html',
	styleUrls: ['./product-change.component.css', '../products.component.scss'],
})
export class ProductChangeComponent implements OnChanges {
	@Input() currentProductGrade: number;
	@Input() scheduledProductGrade: number;

	@Input() allProducts: Product[] = [];

	_gradeOfCard: number;
	@Input() set gradeOfCard(grade) {
		this._gradeOfCard = parseInt(grade, 10);
	}

	SubscriptionChangeDirection = SubscriptionChangeDirection;
	productChangeType: number;

	constructor(private readonly productChangeService: ProductChangeService) {
	}

	ngOnChanges(): void {
		this.setChangeType();
	}

	setChangeType(): void {
		let changeDirection = getChangeDirectionWithUnchanged(this.currentProductGrade, this._gradeOfCard);
		if (changeDirection === SubscriptionChangeDirection.UPGRADE) {
			const freeTierProductGrade = getLowestGradeProduct(this.allProducts).grade;
			// An upgrade can not be performed to the lowest tier, even if no subscription
			// exists yet
			if (this._gradeOfCard === freeTierProductGrade) {
				this.productChangeType = SubscriptionChangeDirection.UNCHANGED;
				return;
			}
			this.productChangeType = SubscriptionChangeDirection.UPGRADE;
			return;
		}

		if (changeDirection === SubscriptionChangeDirection.DOWNGRADE) {
			// If the subscription has been cancelled no further downgrades can be scheduled
			if (!this.scheduledProductGrade) {
				this.productChangeType = SubscriptionChangeDirection.UNCHANGED;
				return;
			}
			changeDirection = getChangeDirectionWithUnchanged(this.scheduledProductGrade, this._gradeOfCard);
		}
		this.productChangeType = changeDirection;
	}

	onClick() {
		this.productChangeService.scheduleProductChange(this._gradeOfCard);
	}
}
