import { Component, OnDestroy } from '@angular/core';
import { ToggleItem } from '../../../../shared/toggle/toggle.component';
import { PlanPaymentInterval, Product, ProductType, Subscription } from 'domain-entities';
import { MatDialog, MatDialogRef } from '@angular/material';
import { ProductService } from '../../../../shared/firebase/product.service';
import { BehaviorSubject, combineLatest, Observable, Subject } from 'rxjs';
import { filter, map, mergeMap, shareReplay, take, takeUntil } from 'rxjs/operators';
import { AuthService } from '../../../../shared/firebase/auth.service';
import { CompanyService } from '../../../../shared/firebase/company.service';
import { ProfileService } from '../../../../shared/firebase/profile.service';
import { SubscriptionWizardComponent } from '../../../shared/dialog/components/subscription-wizard/subscription-wizard.component';
import { SubscriptionService } from '../../../../shared/firebase/subscription.service';
import { Company } from '../../../../shared/models/company.model';
import { ProductChange } from './product-confirm/model/ProductChange';
import { ProductState } from './model/ProductState';
import { ProductChangeService } from './product-change.service';
import {
	getCurrentPlan,
	getLowestGradeProduct,
	getPlanByPaymentInterval,
	getProductByGrade,
	getProductForCurrentPlan,
	getProductForNextPlan,
} from './shared/shared.functions';
import { DecimalPipe } from '@angular/common';
import { SubscriptionChangeType } from './model/SubscriptionChangeType';
import { SubscriptionChangeStatus } from './model/SubscriptionChangeStatus';
import { ActivatedRoute, Router } from '@angular/router';
import { DEFAULT_CURRENCY } from '../../../../shared/constants/subscription';
import { TrackEventType } from '../../../../shared/models/track-event.type';
import { TrackingDispatcherService } from '../../../../shared/tracking/tracking-dispatcher.service';
import { HotjarTrackingService } from '../../../third-party/hotjar-tracking/services/hotjar-tracking.service';
import { Store } from '@ngrx/store';
import { selectProductsTypeSubscription, selectProductsTypeSubscriptionAndTrial } from '../store/selectors/products.selectors';
import { selectSubscriptionState } from '../../../../store/selectors/app.selectors';
import { AppState } from '../../../../store/state/app.state';

const MONTH_PER_YEAR = 12;

@Component({
	selector: 'app-products',
	templateUrl: './products.component.html',
	styleUrls: ['./products.component.scss', '../../../../account/settings/settings.component.scss'],
	providers: [DecimalPipe],
})
export class ProductsComponent implements OnDestroy {
	public items: PlanPaymentIntervalToggleItem[] = [
		{
			translateKey: 'subscription.billingPeriod.yearly.title',
			note: 'subscription.billingPeriod.yearly.note',
			planPaymentInterval: PlanPaymentInterval.YEARLY,
		},
		{
			translateKey: 'subscription.billingPeriod.monthly.title',
			planPaymentInterval: PlanPaymentInterval.MONTHLY,
		},
	];
	PlanPaymentInterval = PlanPaymentInterval;
	selectedToggle: PlanPaymentIntervalToggleItem;
	wizardDialogRef: MatDialogRef<SubscriptionWizardComponent>;
	destroy$: Subject<void> = new Subject();

	company$: Observable<Company>;
	subscription$: Subject<Subscription> = new BehaviorSubject<Subscription>(null);
	products$ = this.store.select(selectProductsTypeSubscriptionAndTrial);
	productState$: Observable<ProductState>;

	productChange$: Observable<ProductChange>;

	get selectedToggleIndex(): number {
		if (!this.selectedToggle) {
			return 0;
		}
		return this.items.findIndex(item => item.planPaymentInterval === this.selectedToggle.planPaymentInterval);
	}

	constructor(
		private readonly authService: AuthService,
		private readonly companyService: CompanyService,
		private readonly profileService: ProfileService,
		private readonly productService: ProductService,
		private readonly subscriptionService: SubscriptionService,
		private readonly subscriptionWizardDialog: MatDialog,
		private readonly productChangeService: ProductChangeService,
		private readonly trackingDispatchService: TrackingDispatcherService,
		private readonly router: Router,
		private readonly decimalPipe: DecimalPipe,
		private readonly hotjarTrackingService: HotjarTrackingService,
		private readonly activeRoute: ActivatedRoute,
		private readonly store: Store<AppState>,
	) {
		this.itemSelected(0, false);
		this.initSubscriptionOfCompany();
		this.initProductState();
		this.initCompany();
		this.initProductChanges();
		this.initConfirmationWatch();
		this.initWizard();
		this.initToggleWatch();
		this.initDirectlyRoutedProduct();
	}

	ngOnDestroy(): void {
		this.destroy$.next();
		this.destroy$.complete();
	}

	public itemSelected(index: number, track: boolean): void {
		this.selectedToggle = this.items[index];
		this.productChangeService.updatePaymentInterval(this.selectedToggle.planPaymentInterval);
		if (!track) {
			return;
		}
		this.trackingDispatchService.emitTrackingEvent(
			TrackEventType.SUBSCRIPTION_PRODUCTS_INTERVAL_TOGGLE,
			{ type: this.selectedToggle.planPaymentInterval },
		);
	}

	private initCompany(): void {
		this.company$ = this.authService.$authState.pipe(mergeMap(user => this.profileService
			.getProfile(user.uid)), mergeMap(profile => this.companyService.getCompany(profile.company)), shareReplay());
	}

	initProductState(): void {
		this.productState$ = combineLatest([this.subscription$, this.products$]).pipe(map(params => {
			const [subscription, allProducts] = params;
			if (!subscription) {
				const defaultProduct = getLowestGradeProduct(allProducts);
				return { currentProduct: defaultProduct, nextProduct: defaultProduct, allProducts };
			}
			const currentProduct = getProductForCurrentPlan(allProducts, subscription);
			const nextProduct = getProductForNextPlan(allProducts, subscription);
			const productState: ProductState = { currentProduct, nextProduct, allProducts };
			return productState;
		}));
	}

	initProductChanges(): void {
		this.productChange$ = this.productChangeService.getProductChanges().pipe(
			filter(
				change => change.changeType === SubscriptionChangeType.NEW_SUBSCRIPTION ||
					change.changeType === SubscriptionChangeType.PRODUCT_CHANGE),
			map(change => change as ProductChange));
	}

	initSubscriptionOfCompany(): void {
		const allSubscription$ = this.subscriptionService.getCompanySubscriptions().pipe();
		combineLatest([this.products$, allSubscription$]).pipe(map(params => {
			const [products, subscriptions] = params;
			return subscriptions.find(sub => getProductForCurrentPlan(products, sub).type === ProductType.SUBSCRIPTION);
		}), takeUntil(this.destroy$)).subscribe(subscription => {
			this.subscription$.next(subscription);
		});
	}

	initToggleWatch(): void {
		combineLatest([this.subscription$, this.products$]).pipe(
			map(([subscription, products]) => {
				if (!subscription) {
					return;
				}
				const plan = getCurrentPlan(products, subscription);
				this.selectedToggle = this.items.find(item => item.planPaymentInterval === plan.paymentInterval);
			}), takeUntil(this.destroy$)).subscribe();
	}

	private async openSubscriptionWizard(productChange: ProductChange): Promise<void> {
		const products = await this.products$.pipe(take(1)).toPromise();
		const initData = {
			products, productChange,
		};
		if (!initData) {
			return;
		}
		this.wizardDialogRef = this.subscriptionWizardDialog.open(SubscriptionWizardComponent, {
			data: initData,
			disableClose: true,
			panelClass: 'no-padding-dialog',
		});
	}

	private initConfirmationWatch(): void {
		this.productChange$.pipe(
			filter(changes =>
				changes.changeType === SubscriptionChangeType.PRODUCT_CHANGE ||
				changes.changeType === SubscriptionChangeType.NEW_SUBSCRIPTION),
			filter(changes => changes.status === SubscriptionChangeStatus.CONFIRMED),
			takeUntil(this.destroy$),
		).subscribe(() => {
			if (this.wizardDialogRef) {
				this.wizardDialogRef.close();
			}
			this.router.navigate(['settings/subscription']);
		});
	}

	private initWizard(): void {
		this.productChange$.pipe(
			filter(changes => changes.changeType === SubscriptionChangeType.NEW_SUBSCRIPTION && changes.status === SubscriptionChangeStatus.OPEN),
			takeUntil(this.destroy$))
			.subscribe(changes => {
				this.trackWizardOpen(changes);
				this.openSubscriptionWizard(changes);
			});
	}

	getCostOfPlan(products: Product[], grade: number, paymentInterval: PlanPaymentInterval): string {
		const product = getProductByGrade(products, grade);
		const plan = getPlanByPaymentInterval(product, paymentInterval);
		const factorForMonthlyCostDisplay = paymentInterval === PlanPaymentInterval.MONTHLY ? 1 : MONTH_PER_YEAR;
		const costPerUser = plan.costPerUser[DEFAULT_CURRENCY] / factorForMonthlyCostDisplay;

		return this.decimalPipe.transform(costPerUser, '1.2-2');
	}

	private trackWizardOpen(change: ProductChange): void {
		if (change.to.product.grade === 1) {
			this.trackingDispatchService.emitTrackingEvent(TrackEventType.SUBSCRIPTION_PRODUCTS_WIZARD_OPEN, { type: 'pro' });
		} else if (change.to.product.grade === 2) {
			this.trackingDispatchService.emitTrackingEvent(TrackEventType.SUBSCRIPTION_PRODUCTS_WIZARD_OPEN, { type: 'premium' });
		}
	}

	private async initDirectlyRoutedProduct(): Promise<void> {
		const product: string = this.activeRoute.snapshot.queryParams['product'];
		const term: string = this.activeRoute.snapshot.queryParams['term'];

		if (!term && !product) {
			return;
		}

		const [activeSubscription, products, isTrialSubscription] = await combineLatest([
			this.subscriptionService.getCurrentSubscription(),
			this.store.select(selectProductsTypeSubscription),
			this.store.select(selectSubscriptionState).pipe(map(state => {
				return !!(state && state.trialSubscription);
			})),
		]).pipe(
			take(1),
		).toPromise();

		if (activeSubscription && !isTrialSubscription) {
			this.router.navigate(['settings/subscription']);
			return;
		}

		if (term) {
			this.selectedToggle = this.items
				.find(item => item.planPaymentInterval === term.toUpperCase()) || this.items[0];
			this.productChangeService.updatePaymentInterval(this.selectedToggle.planPaymentInterval);
		}

		if (product) {
			const productName = product.toLowerCase();
			const selectedProduct = products
				.find(productTypeSubscription => productTypeSubscription.shortName.toLowerCase() === productName);

			if (!selectedProduct) {
				return;
			}
			this.productChangeService.scheduleProductChange(selectedProduct.grade);
		}
	}
}

interface PlanPaymentIntervalToggleItem extends ToggleItem {
	planPaymentInterval: PlanPaymentInterval;
}

